.rate-popup {
  box-sizing: border-box;
  position: fixed;
  width: 473px;
  height: 780px;
  left: 1045px;
  top: 5px;
  z-index: 120;
  background: #ffffff;
  border-right: 1px solid #f7f7f7;
  /* box-shadow: -7px 4px 31px 24px rgba(0, 0, 0, 0.03); */
}
.body {
  position: fixed;
  width: 1519px;
  height: 1024px;
  z-index: 110;
  left: 0px;
  top: 0px;

  background: rgba(207, 206, 206, 0.61);
  backdrop-filter: blur(8px);
}
.ratepopup {
  /* background: rgba(207, 206, 206, 0.61); */
  backdrop-filter: blur(50px);
}

h4 {
  position: absolute;
  width: 239px;
  height: 33px;
  left: 38px;
  top: 50px;
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  display: flex;
  align-items: center;
  color: #000000;
}

.rate {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 20px;
  position: absolute;
  width: 235px;
  height: 31px;
  left: 30px;
  top: 130px;
  transform: matrix(1, 0, 0, 1, 0, 0);
}
.rate:not(:checked) > input {
  position: absolute;
  top: -9999px;
}
.rate:not(:checked) > label {
  float: right;
  width: 1em;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  font-size: 30px;
  color: rgb(241, 241, 241);
}
.rate:not(:checked) > label:before {
  content: " ";
}
.rate > input:checked ~ label {
  color: #ffc700;
}
.rate:not(:checked) > label:hover,
.rate:not(:checked) > label:hover ~ label {
  color: #deb217;
}
.rate > input:checked + label:hover,
.rate > input:checked + label:hover ~ label,
.rate > input:checked ~ label:hover,
.rate > input:checked ~ label:hover ~ label,
.rate > label:hover ~ input:checked ~ label {
  color: #c59b08;
}

.review-note-header {
  top: 165px;
  font-family: "Avenir Next";
}
/* #add-note-input {
  width: 409px;
  height: 402px;
} */
.input-box-rate {
  box-sizing: border-box;
  position: absolute;
  width: 400px;
  height: 330px;
  left: 32px;
  top: 250px;
  background: #f6f6f6;
  border: 1px solid #d8d8d8;
  border-radius: 8px;
  padding-bottom: 50%;
  padding-right: 0%;
  padding-top: 0%;
  font-size: 0.51em;
}

.input-box-rate::placeholder {
  position: absolute;
  width: 172px;
  height: 27px;
  left: 24px;
  top: 24px;
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  display: flex;
  align-items: center;
  color: #a6a6a6;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 24px;
  position: absolute;
  width: 324px;
  height: 48px;
  left: 45px;
  top: 600px;
}
.save-button-rate {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 36px;
  gap: 8px;
  width: 177px;
  height: 48px;
  background: #305ba5;
  border-radius: 8px;
  cursor: pointer;
  flex: none;
  order: 0;
  flex-grow: 0;
  color: #ffffff;
}
.cancel-button-rate {
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 36px;
  gap: 8px;
  width: 123px;
  height: 48px;
  border: 1px solid #305ba5;
  border-radius: 8px;
  flex: none;
  order: 0;
  flex-grow: 0;
}
