.remove-profilepic-body {
  position: fixed;
  width: -webkit-fill-available;
  height: 1024px;
  z-index: 110;
  left: 0px;
  top: 0px;

  background: rgba(207, 206, 206, 0.61);
  backdrop-filter: blur(8px);
}
.RemoveProfilePic_popup {
  box-sizing: border-box;
  position: fixed;
  width: 25%;
  height: 20%;
  left: 35%;
  top: 24%;
  z-index: 120;
  background: #ffffff;
  border-right: 1px solid #f7f7f7;
  border-radius: 12px;
}
.remove-note-popup {
  position: relative;
  left: 16px;
  top: 15px;
  font-size: 18px;
}
.buttons_removeProfilePopUp {
  /* position: relative;
    left: 35%;
    top: 45%;
    width: fit-content;
    gap:25px; */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 24px;
  position: absolute;
  width: 324px;
  height: 48px;
  left: 30px;
  top: 90px;
}
.RemoveFile_removeProfilePopUp {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 36px;
  gap: 8px;
  width: 177px;
  height: 48px;
  background: #305ba5;
  border-radius: 8px;
  cursor: pointer;
  flex: none;
  order: 0;
  flex-grow: 0;
  color: #ffffff;
}
.CancelFile_removeProfilePopUp {
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 36px;
  gap: 8px;
  width: 123px;
  height: 48px;
  border: 1px solid #305ba5;
  border-radius: 8px;
  flex: none;
  order: 0;
  flex-grow: 0;
}
@media screen and (max-width: 440px) {
  .RemoveProfilePic_popup {
    width: 50%;
    height: 30%;
    left: 25%;
    top: 24%;
  }
  .buttons_removeProfilePopUp {
    left: -42px;
  }
  .RemoveFile_removeProfilePopUp {
    width: 100px;
  }
  .CancelFile_removeProfilePopUp {
    width: 100px;
  }
}
@media screen and (max-width: 380px) {
  .CancelFile_removeProfilePopUp {
    width: 90px;
  }
  .RemoveFile_removeProfilePopUp {
    width: 90px;
  }
  .buttons_removeProfilePopUp {
    left: -55px;
    top:115px;
}
}

@media screen and (max-width: 330px) {
.profile-pic {
    left: 40px;
}
.CancelFile_removeProfilePopUp {
    width: 80px;
  }
  .RemoveFile_removeProfilePopUp {
    width: 80px;
  }
  .buttons_removeProfilePopUp {
    left: -67px;
    top:125px;
}

}