  /* font-size: 3rem; */
  padding-left: 60px;
  flex-direction: column;
  background-color: #fcffff;
}
.settings {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  flex-direction: column;
  background-color: #fcffff;
}
.home {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  background-color: #fcffff;
  width: 1475px;
  flex-direction: column;

  padding-left: 50px;
  color: #cfcfcf;
}

.saved {
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-items: center;
