#UserName_Nav {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  position: relative;
  width: 79px;
  margin-left: 6px;
}
.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  position: absolute;
  top: 7rem;
  display: none;
}
.menu-close {
  margin-left: 2rem;
  font-size: 2rem;
  position: absolute;
  right: 12rem;
  top: 12px;
  display: none;
}
.nav-menu {
  background-color: #ffffff;
  background-color: var(--navBg);
  width: 256px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  top: 100px;
  left: 0;
  transition: 350ms;
  border-right: 2px solid #f7f7f7;
  right: 0.05rem;
  z-index: 100;
  height: -webkit-fill-available;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 16px 8px 16px;
  list-style: none;
  height: 50px;
}

.nav-text a {
  text-decoration: none;
  color: #a7a7a7;
  font-size: 18px;
  width: -webkit-max-content;
  width: max-content;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

